  }, [_vm._v("\n  6.1 用户行为要求"), _c('br'), _vm._v("\n\n  你应当对你使用本软件及相关服务的行为负责，除非法律允许或者经“融资讯APP”事先书面许可，你使用“融资讯APP”软件及相关服务不得具有下列行为："), _c('br'), _vm._v("\n\n  6.1.1 使用未经“融资讯APP”授权或许可的任何插件、外挂、系统或第三方工具对“融资讯APP”软件及相关服务的正常运行进行干扰、破坏、修改或施加其他影响。"), _c('br'), _vm._v("\n\n  6.1.2 利用或针对“融资讯APP”软件及相关服务进行任何危害计算机网络安全的行为，包括但不限于："), _c('br'), _vm._v("\n\n  （1）非法侵入他人网络、干扰他人网络正常功能、窃取网络数据等危害网络安全的活动；"), _c('br'), _vm._v(" \n\n  （2）提供专门用于从事侵入网络、干扰网络正常功能及防护措施、窃取网络数据等危害网络安全活动的程序、工具；"), _c('br'), _vm._v(" \n\n  （3）明知他人从事危害网络安全的活动的，为其提供技术支持、广告推广、支付结算等帮助；"), _c('br'), _vm._v("\n\n  （4）使用未经许可的数据或进入未经许可的服务器/账号；"), _c('br'), _vm._v("\n\n  （5）未经允许进入公众计算机网络或者他人计算机系统并删除、修改、增加存储信息；"), _c('br'), _vm._v(" \n\n  （6）未经许可，企图探查、扫描、测试“融资讯APP”系统或网络的弱点或其它实施破坏网络安全的行为；"), _c('br'), _vm._v("\n\n  （7）企图干涉、破坏“融资讯APP”系统或网站的正常运行，故意传播恶意程序或病毒以及其他破坏干扰正常网络信息服务的行为；"), _c('br'), _vm._v(" \n\n  （8）伪造TCP/IP数据包名称或部分名称；"), _c('br'), _vm._v("\n\n  （9）对“融资讯APP”软件及相关服务进行反向工程、反向汇编、编译或者以其他方式尝试发现本软件的源代码；"), _c('br'), _vm._v("\n\n  （10）恶意注册“融资讯APP”软件及相关服务的账号，包括但不限于频繁、批量注册账号；"), _c('br'), _vm._v("\n\n  （11）违反法律法规、本协议、“融资讯APP”的相关规则及侵犯他人合法权益的其他行为。"), _c('br'), _vm._v("\n\n  6.1.3 如果“融资讯APP”有理由认为你的行为违反或可能违反上述约定的，“融资讯APP”可独立进行判断并处理，且有权在不事先通知的情况下终止向你提供服务，并追究相关法律责任。"), _c('br'), _vm._v("\n\n  6.2 信息内容规范"), _c('br'), _vm._v("\n\n  6.2.1 你按规定完成实名认证后，可以以注册账号或“融资讯APP”合作平台账号登录“融资讯APP”发布内容、跟帖评论等。"), _c('br'), _vm._v("\n\n  6.2.2 “融资讯APP”致力使发布信息、跟帖评论成为文明、理性、友善、高质量的意见交流。在推动发布信息、互动交流、跟帖评论业务发展的同时，不断加强相应的信息安全管理能力，完善发布信息、跟帖评论自律，切实履行社会责任，遵守国家法律法规，尊重公民合法权益，尊重社会公序良俗。"), _c('br'), _vm._v("\n\n  6.2.3 你评论、发布、传播的内容应自觉遵守宪法法律、法规、遵守公共秩序，尊重社会公德、社会主义制度、国家利益、公民合法权益、道德风尚和信息真实性等要求。你同意并承诺不制作、复制、发布、传播法律、法规禁止的下列信息内容："), _c('br'), _vm._v("\n\n  （1）反对宪法确定的基本原则的；"), _c('br'), _vm._v("\n\n  （2）危害国家安全，泄露国家秘密的；"), _c('br'), _vm._v("\n\n  （3）颠覆国家政权，推翻社会主义制度、煽动分裂国家、破坏国家统一的；"), _c('br'), _vm._v("\n\n  （4）损害国家荣誉和利益的；"), _c('br'), _vm._v("\n\n  （5）宣扬恐怖主义、极端主义的；"), _c('br'), _vm._v("\n\n  （6）宣扬民族仇恨、民族歧视，破坏民族团结的；"), _c('br'), _vm._v("\n\n  （7）煽动地域歧视、地域仇恨的；"), _c('br'), _vm._v("\n\